import Vue from 'vue'

export default {
  retrieve (force, cb, errorCb) {
    return Vue.http.get('extensions/retrieve', { force: force ? 1 : 0 })
      .then(({ data }) => cb(data), response => errorCb(response))
  },
  download (extensions, cb, errorCb) {
    return Vue.http.get('extensions/download', { extensions })
      .then(({ data }) => cb(data.downloadUrl), response => errorCb(response))
  },
  install (extensions, cb, errorCb) {
    return Vue.http.get('extensions/install', { extensions })
      .then(({ data }) => cb(data), response => errorCb(response))
  },
  uninstall (extensions, cb, errorCb) {
    return Vue.http.get('extensions/uninstall', { extensions })
      .then(cb, response => errorCb(response))
  },
  toggle (extensions, cb, errorCb) {
    return Vue.http.get('extensions/toggle', { extensions })
      .then(({ data }) => cb(data), response => errorCb(response))
  }
}



