const state = {
  show: false,
  text: '',
  icon: ''
}

const defaults = {
  text: '',
  icon: ''
}

const mutations = {
  SHOW_MODAL_CONFIRM (state, {
    text = defaults.text,
    icon = defaults.icon
  } = defaults) {
    state.show = true
    state.text = text
    state.icon = icon
  },
  HIDE_MODAL_CONFIRM (state) {
    state.show = false
  }
}

export default {
  state,
  mutations
}



